<template>
    <Modal
            v-bind:adres="souce"
            v-model:isOpen="isOpen"
    />
    <div ref="observer"></div>
    <div

            class="dorian"
            v-bind:class="{darkbaby:isDark}">
<!--        <div>-->
<!--            <img @click="openModal" class="item" src="@/assets/p1.png" alt="1">-->
<!--            <img @click="openModal" class="item" src="@/assets/p2.png" alt="2">-->
<!--            <img @click="openModal" class="item" src="@/assets/p3.png" alt="3">-->
<!--            <img @click="openModal" class="item" src="@/assets/p4.png" alt="4">-->
<!--            <img @click="openModal" class="item" src="@/assets/p5.png" alt="5">-->
<!--            <img @click="openModal" class="item" src="@/assets/p6.png" alt="6">-->
<!--        </div>-->
        <div >
            <img
                    v-for="n in 6"
                    :src="require(`@/assets/p${n}.png`)"
                    :alt="n"
                    @click="openModal"
                    class="item"
                    :style="{opacity:opac,  transform: `translateX(${progTrans}%)`}"



            >
        </div>
    </div>

</template>

<script>

    import Modal from "../Modal";
    export default {
        components:{
            Modal,

        },
        data(){
            return{
                souce:'',
                isOpen:false,
                opac:0,
                progTrans:100

            }
        }
        ,
        methods:{
            openModal(img){
                this.souce=img.target.src;
                this.isOpen=true;
                /*console.log(this.$refs.text);*/
                /*console.log(img.target.src);*/
                /*this.isOpen=true*/
               /* console.log(img.target.src);*/
            },

        },
        props: {
            isDark: {
                type: Boolean

            },


        },
        mounted(){

            const options = {
                /* root: document.querySelector('#scrollArea'),*/
                rootMargin: '0px',
                threshold: 1.0
            }
            const callback= (entries,observer)=> {
                 if(entries[0].isIntersecting)
                 {
                 this.opac=1;
                this.progTrans=0;

                 }


               /* console.log(entries[0].rootBounds.height);*/
               /* console.log(document.body.scroll);*/

            }
            const observer = new IntersectionObserver(callback, options);
            observer.observe(this.$refs.observer);
        }

    }
</script>

<style scoped>

    /*third*/
    .darkbaby{
        background-color: #616161 !important;
    }
    .dorian{
        background-color: #f1f1f1;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 2%;
        padding-bottom: 2%;

    }
    .dorian>div{
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto;

    }
    .item{
        margin: 1%;
        width: 370px;
        height: 300px;
        transform: translateX(100%);
        transition: transform 3s,opacity 3s, filter 1s ;




    }


    .dorian img:nth-child(1):hover{
        filter: drop-shadow(16px 16px 20px rgb(0, 225, 255)) blur(5px);
    }
    .dorian img:nth-child(2):hover{
        filter: drop-shadow(16px 16px 20px rgb(207, 221, 8)) contrast(200%);
    }
    .dorian img:nth-child(3):hover{
        filter: drop-shadow(16px 16px 20px rgb(0, 225, 255)) grayscale(100%);
    }
    .dorian img:nth-child(4):hover{
        filter: drop-shadow(16px 16px 20px rgb(0, 225, 255)) hue-rotate(90deg);
    }
    .dorian img:nth-child(5):hover{
        filter: drop-shadow(16px 16px 20px red) invert(75%);
    }
    .dorian img:nth-child(6):hover{
        filter: drop-shadow(16px 16px 20px rgb(0, 225, 255)) sepia(100%);

    }
    /*third*/
</style>