<template>



        <div id="myModal"
             class="modal"
             v-if="isOpen">


            <div class="modal-content">
                <span class="close" @click="closeBabe">&times;</span>


                <img :src="adres" />
            </div>
        </div>


</template>

<script>

    export default {
        data(){
            return{

                toClose:false

            }
        }
        ,
        props:{
            adres:{
                type:String,
                required:true
            },
            isOpen:{
                type:Boolean
            }

        },
        methods:{
            closeBabe(){
                this.$emit('update:isOpen',false);
            },


        },


        watch:{
            adres() {
                this.toClose=true;

            }

        }



    }
</script>

<style scoped>

    .modal {
        display: block;
        position: fixed;
        z-index: 1;
        padding-top: 80px;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: rgb(0,0,0);
        background-color: rgba(0,0,0,0.4);
    }


    .modal-content {
        background-color: #fefefe;
        margin: auto;
        padding: 20px;
        border: 1px solid #888;
        /*width: auto;*/
        /*height: 90%;*/
        height: 70vh;
        width: 70vw;
        text-align: center;
    }
    .modal-content img {
        /*width: 90%;*/
        max-width: 90%;
        height: 100%;
        /*max-width: 100%;*/
        object-fit: cover;
    }

    /* Кнопка закрытия */
    .close {
        color: #aaaaaa;
        float: right;
        font-size: 28px;
        font-weight: bold;
    }

    .close:hover,
    .close:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
    }
</style>