<template>
    <div class="sticker">
        <a name="her"></a>
        <h2>Contact us</h2>
        <form action="#" method="post" @submit.prevent>
            <input type="text" v-bind:class="{pleaseinsert: isName}"  v-bind:value="name" @input="inputName" id="name" placeholder="Name">
            <input type="text" v-bind:class="{pleaseinsert: isEmail}" v-bind:value="email" @input="inputEmail" id="email" placeholder="Email">
            <input type="text"  id="subject" placeholder="Subject">
            <textarea  id="message" v-bind:class="{pleaseinsert: isText}" v-bind:value="textarea" @input="inputText" cols="30" rows="10" placeholder="Your Message"></textarea>
            <button type="submit" @click="checkingMe">submit</button>
        </form>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                name:'',
                email: '',
                textarea:'',
                isName:false,
                isEmail:false,
                isText:false
            }
        },
        methods:{
            inputName(event){
                this.name=event.target.value;
                this.isName=false;

            },

            inputEmail(event){

                this.email=event.target.value;
                this.isEmail=false;
            },
            inputText(event){

                this.textarea=event.target.value;
                this.isText=false;
            },

            checkingMe(){
               if(!this.name)
               {
                   this.isName=true;
                   alert('Введите имя');

               }
               else if(!this.email)
                {
                    this.isEmail=true;
                    alert('Введите почту');

                }
               else if(this.email){
                    if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(this.email)) {
                       alert('Что с почтой не то -_-');
                   }
               }
               else if(!this.textarea)
               {
                   this.isText=true;
                   alert('Заполните текст сообщения');

               }

            }



        },




    }
</script>

<style scoped>
    .pleaseinsert{
        border:1px solid red;
    }
    /*fourth*/
    .sticker{
        padding-top: 2%;
        text-align: center;
        padding-bottom: 2%;
    }
    .sticker h1{
        font-size: 36px;
        line-height: 1.2;
        font-weight: 400;
        letter-spacing: 0.05em;
        color: #413d4b;
    }
    .sticker form{
        display: flex;
        flex-direction: column;
        max-width: 460px;

        margin-left: auto;
        margin-right: auto;

    }
    form input{
        border: 1px solid #e6e6e6;
        height: 35px;
        margin-top: 3%;
        padding: 1%;
        outline: none;

    }
    form textarea{
        border: 1px solid #e6e6e6;
        /*width: 460px;*/
        height: 200px;
        margin-top: 3%;
        padding: 1%;
        resize: none;
        outline: none;
    }
    .sticker button{
        font-size: 16px;
        line-height: 1.2;
        font-weight: 400;
        color: #6bc2f1;
        width: 120px;
        height: 40px;
        background-color: #ffffff;
        border: 1px solid #6bc2f1;
        cursor: pointer;
        margin-left: auto;
        margin-right: auto;
        margin-top: 3%;

    }
    .sticker button:hover{
        background-color: #6bc2f1;
        color: #ffffff;

    }
    /*fourth*/
</style>