<template>
    <progress-bar :value="progress"/>
    <div class="app" ref="text" @scroll="onScroll" >
        <Header @create="onDark"/>
        <Article

                v-bind:isDark="isDark"
                v-bind:class="{darkbaby:isDark}"/>
        <Footer/>
    </div>
</template>

<script>
    import Header from "./components/Header";
    import Article from "./components/Article";
    import Footer from "./components/Footer";
    import ProgressBar from './components/ProgressBar';
    export default {
        components:{

            Header,
            Article,
            Footer,
            ProgressBar,
        },
        data(){
            return{
                progress:0,
                isDark:false
            }
        },
        methods:{
            onScroll () {
                const progress = this.$refs.text.scrollTop / (this.$refs.text.scrollHeight - this.$refs.text.clientHeight)

                if (progress > 1) {
                    this.progress = 1
                } else if (progress < 0) {
                    this.progress = 0
                } else {
                    this.progress = progress
                }
            },
            onDark(lol){
                this.isDark=lol;
               /* console.log(this.isDark);*/
            }
        }
    }

</script>


<style >
    @import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

    .darkbaby{
        background-color: #616161;
    }

    .app {
        height: 100vh;
        overflow-y: scroll;
    }
    body{
        overflow:hidden;
    }
    .app::-webkit-scrollbar {
        width: 0px;
        background: transparent;
    }

    *{
        margin: 0;
        padding: 0;
        font-family: 'Roboto', sans-serif;

    }
    button{
        width: 140px;
        height: 46px;
        background-color: #6bc2f1;
        border: none;
        color: white;
        text-transform: uppercase;
        cursor: pointer;
    }
    @media(max-width:1205px){
        .best h1{
            font-size: 25px !important;
            line-height: 1;
        }
        .best h2{
            margin-top: 3%;
            font-size: 13px !important;
            line-height: 1;

        }
        .best p{

            font-size: 12px;
            line-height: 20px;

        }
        .dedl {
            padding: 2%;

        }
        .item{
            width: 300px;
        }
        .dorian{
            text-align: center;
        }
        header{

            background-size:cover !important;

        }
    }

    @media(max-width:750px){
        .le>ul{
            display: none;
        }
        ul{
            margin-left: 0!important;
        }

        .best{
            text-align: center;
            max-width: 100%;
        }
        .best button{
            width:50%;
            margin-top: 1%;

        }
        .ded{
            flex-wrap: wrap;
        }
        .dedr{
            text-align: center;
        }
        .dedr{
            margin-left: auto;
            margin-right: auto;
        }
        .baby{
            margin-top: 1%;
        }
        img{
            max-width: 90%;
        }
        /* form textarea{
             width: 90%;
         }*/
        .ciao{
            flex-wrap: wrap;
        }

        .blue h3{

            font-size: 10px !important;
            line-height: 1.2;
            font-weight: 400;


        }
        .blue h2{

            font-size: 13px !important;
            line-height: 1.2;


        }
        .blue p{

            font-size: 9px !important;
            line-height: 15px;

        }


        /*burger*/

        #menu__toggle {
            opacity: 0;
            position: fixed;
        }

        .menu__btn {
            display: flex;
            align-items: center;
            position: fixed;
            top: 2.5%;
            left: 20px;
            width: 26px;
            height: 26px;
            cursor: pointer;
            z-index: 2;
        }

        .menu__btn > span,
        .menu__btn > span::before,
        .menu__btn > span::after {
            display: block;
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: #616161;
        }
        .menu__btn > span::before {
            content: '';
            top: -8px;
        }
        .menu__btn > span::after {
            content: '';
            top: 8px;
        }


        .menu__box {
            display: block;
            position: fixed;
            visibility: hidden;
            top: 0;
            left: -100%;
            width: 300px;
            height: 100%;
            margin: 0;
            padding: 80px 0;
            list-style: none;
            text-align: center;
            background-color: #ECEFF1;
            box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
            z-index: 1;
        }

        .menu__item {
            display: block;
            padding: 12px 24px;
            color: #333;
            font-family: 'Roboto', sans-serif;
            font-size: 20px;
            font-weight: 600;
            text-decoration: none;
        }
        .menu__item:hover {
            background-color: #CFD8DC;
        }
        #menu__toggle:checked ~ .menu__btn > span {
            transform: rotate(45deg);
        }
        #menu__toggle:checked ~ .menu__btn > span::before {
            top: 0;
            transform: rotate(0);
        }
        #menu__toggle:checked ~ .menu__btn > span::after {
            top: 0;
            transform: rotate(90deg);
        }
        #menu__toggle:checked ~ .menu__box {
            visibility: visible;
            left: 0;
        }
        /*burger*/
    }
</style>
